body {
  margin: 0;
  padding: 0;

}



.wrap{
  max-width: 960px;
  margin: 0 auto;
  padding: 10px;
}
.container{
  display: grid;
  grid-gap: 10px;
  grid-template-columns:repeat(auto-fill, minmax(240px, 1fr));
}

.info{
  background-color: aqua;
  padding: 20px;
}

.info:nth-child(1){
   grid-column: 1/-1; 
}

@media only screen and (max-width: 400px) {
  .info:nth-child(1){
    background-color: lightblue;

  }
}
